import React from "react";
import GoogleMapReact from "google-map-react";

const Map = ({ locations, center, zoomLevel }) => (
    <div className="map" id="map">
      <h2 className="map-h2">Locations To Practice Self-Care Near You</h2>
  
      <div className="google-map">
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyDolez9brYVb7NuBDDcnonx_dZwT8pxlWw' }}
          defaultCenter={center}
          defaultZoom={zoomLevel}
        >
          {locations.map((location =>
            <LocationPin
              lat={location.lat}
              lng={location.lng}
              text={location.name}
              id={location.id}
              link = {location.link}
            />
            ))}
        </GoogleMapReact>
      </div>
    </div>
  )
  
const LocationPin = ({text, id, link, $hover}) => {

  const handleClick = () => {
    window.open(link, '_blank')
  }
    return (
      <div className={$hover ? "pin hover":"pin"}>
          <i class="fa fa-map-pin" aria-hidden="true" title={text} onClick={handleClick}>
            {id}
          </i>
      </div>
    );
}

export default Map;
